//* Masonry galery */
  .masonry-list {
    display: flex;
    padding-inline-start: 0;
    justify-content: center;
  }
  .masonry-list__item {
    display: flex;
    position: relative;
    align-items: center;
  }
  .inside-list__item {
    display: flex;
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    margin-right: 1px;

    @media screen and (min-width: 768px){
      margin-right: 3px;
    }
    @media screen and (min-width: 1200px){
      margin-right: 8px;
    }
  
    &:hover, &:focus {
      position: sticky;
      z-index: 100;
      transform: scale(1.5);
    }
  }
  .inside-list__first-item {
    margin-bottom: 1px;

    @media screen and (min-width: 768px){
      margin-bottom: 3px;
    }
    @media screen and (min-width: 1200px){
      margin-bottom: 8px;
    }
  }
  .inside-list__item--left-positioned {
    display: inline-block;
  }
  .masonry__photo {
    border-radius: 5px;
    width: 40px;
    height: 40px;

    @media screen and (min-width: 768px){
      width: 86px;
      height: 86px;
    }
    @media screen and (min-width: 1200px){
      width: 140px;
      height: 140px;
      border-radius: 20px;
    }
  }
  .masonry__photo--bigger {
    width: 81px;
    height: 81px;

    @media screen and (min-width: 768px){
      width: 175px;
      height: 175px;
    }
    @media screen and (min-width: 1200px){
      width: 285px;
      height: 285px;
    }
  }
